'use strict';

var angular = require(44);

// brfs requires this to be on its own line


// @ngInject
function modalManagerFactory($document, $compile, $q, $animate, $rootScope) {

  var body = $document.find('body').eq(0);
  var backdropElement = angular.element('<div class="mt-backdrop mt-animation-enter-leave__fade"></div>'),
    modalLinkFn = $compile("<div class=\"mt-modal\">\n  <div class=\"mt-modal__container\">\n    <div class=\"mt-modal__frame\">\n      <h2 class=\"mt-modal__title\">{{title}}</h2>\n\n      <div class=\"mt-modal__content\">\n        <!-- the following div is the only way I found to fix a rendering problem in Chrome and Safari mobile -->\n        <!-- where the hidden part of the overflowing zone is never rendered -->\n        <div ng-include=\"contentTemplateUrl\"></div>\n      </div>\n\n      <div class=\"mt-modal__commands\">\n        <div role=\"button\" tabindex=\"0\" class=\"mt-modal__button\"\n             ng-repeat=\"command in commands\"\n             ng-class=\"{secondary: !command.primary}\"\n             ng-click=\"close(command.name)\">\n          {{command.label}}\n        </div>\n      </div>\n    </div>\n  </div>\n</div>");

  var open = function(options) {
    $animate.enter(backdropElement, body);

    function leave(modalElement) {
      return $q.all([
        $animate.leave(backdropElement),
        $animate.leave(modalElement)
      ]);
    }

    var scope = angular.extend($rootScope.$new(true), options);

    return $q(function(resolve) {
      modalLinkFn(scope, function(modalElement) {

        scope.close = function(command) {
          leave(modalElement).then(function() {
            resolve(command);
          });
        };

        $animate.enter(modalElement, body);
      });
    });
  };

  /**
   * @name modalManager
   */
  var modalManager = {
    open: open
  };

  return modalManager;
}

module.exports = modalManagerFactory;