'use strict';

var pull = require(49);

// brfs requires this to be on its own line


// @ngInject
function notificationCenterDirective(notificationCentersRegistry, directivesRegistryHelper) {

  return {
    restrict: 'E',
    template: "<div class=\"mt-notifications-center__container mt-js-animation__slide-fold from-right\"\n     ng-repeat=\"notification in notificationCenterCtrl.notifications\">\n  <!-- the next div is required for proper height animation by containing the padding it allows to shrink the parent height to 0 -->\n  <!-- normally even with height 0 the padding is shown -->\n  <div class=\"mt-notifications-center__container__padding\">\n    <div role=\"button\" tabindex=\"0\" class=\"mt-notification\" ng-switch on=\"notification.type\"\n         ng-class=\"{error: notification.type === 'error'}\" ng-click=\"notificationCenterCtrl.close(notification)\">\n      <div class=\"mt-notification__content\" ng-switch-when=\"error\">\n        <div class=\"mt-notification__text\">\n          {{notification.data.message}}\n        </div>\n      </div>\n      <div class=\"mt-notification__content\" ng-switch-when=\"comingNext\">\n        <div class=\"mt-notification__text\">\n          <div class=\"mt-coming-next__group\">\n            <div class=\"mt-coming-next__header\">It was:</div>\n            <div class=\"mt-coming-next__title\">{{notification.data.current}}</div>\n          </div>\n          <div class=\"mt-coming-next__group next\" ng-show=\"notification.data.next\">\n            <div class=\"mt-coming-next__header\">Coming next:</div>\n            <div class=\"mt-coming-next__title\">{{notification.data.next}}</div>\n          </div>\n        </div>\n        <div class=\"mt-notification__thumbnail\" ng-show=\"notification.data.imageUrl\">\n          <div class=\"mt-notification__image\"\n               ng-style=\"{'background-image': 'url({{notification.data.imageUrl}})'}\"></div>\n        </div>\n      </div>\n    </div>\n  </div>\n</div>",
    controllerAs: 'notificationCenterCtrl',
    controller: /*@ngInject*/ function($scope, $element, $attrs) {

      function close(notification) {
        pull(notificationCenterCtrl.notifications, notification);
      }

      /**
       * @param {Object} data the data structure passed to the template
       * @returns {Function} a callback to close the coming next notification
       */
      function comingNext(data) {
        var notification = {type: 'comingNext', data: data};
        notificationCenterCtrl.notifications.unshift(notification);
        return function closeComingNext() {
          close(notification);
        };
      }

      function error(message) {
        notificationCenterCtrl.notifications.unshift({type: 'error', data: {message: message}});
      }

      function activate() {
        directivesRegistryHelper.install(notificationCenterCtrl, notificationCentersRegistry, 'name', $scope,
          $attrs);
      }

      var notificationCenterCtrl = this;

      notificationCenterCtrl.notifications = [];

      notificationCenterCtrl.comingNext = comingNext;
      notificationCenterCtrl.error = error;
      // publish the close function to be used in the template
      notificationCenterCtrl.close = close;

      activate();
    }
  };
}

module.exports = notificationCenterDirective;